h1, h2, h3 {
  margin-top: 0;
  line-height: 1.1;
}

h1 {
  font-size: 1.5rem;
  margin-bottom: 0.1875rem;
}
@media (min-width: 71.875em) {
  h1 {
    font-size: 1.75rem;
  }
}

a, a:visited, a:active {
  text-decoration: none;
}

:root {
  --font-inter: "Inter", sans-serif;
}

:root {
  --limegreen: hsl(163, 72%, 41%);
  --brightred: hsl(356, 69%, 56%);
  --facebook: hsl(208, 92%, 53%);
  --twitter: hsl(203, 89%, 53%);
  --instagram-start: hsl(37, 97%, 70%);
  --instagram-end: hsl(329, 70%, 58%);
  --youtube: hsl(348, 97%, 39%);
  --toggle-background-start: hsl(210, 78%, 56%);
  --toggle-background-end: hsl(146, 68%, 55%);
  --toggle: hsl(230, 22%, 74%);
  --dark-bg: hsl(230, 17%, 14%);
  --dark-top-bg: hsl(232, 19%, 15%);
  --dark-card: hsl(228, 28%, 20%);
  --dark-text1: hsl(228, 34%, 66%);
  --dark-text2: hsl(0, 0%, 100%);
  --light-bg: hsl(0, 0%, 100%);
  --light-top-bg: hsl(225, 100%, 98%);
  --light-card: hsl(227, 47%, 96%);
  --light-text1: hsl(228, 12%, 44%);
  --light-text2: hsl(230, 17%, 14%) ;
}

.container {
  padding: 0 1.5625rem;
  max-width: 68.75rem;
  margin: 0 auto 2.875rem;
}
@media (min-width: 71.875em) {
  .container {
    padding: 0;
  }
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*, *::before, *::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--font-inter);
  background: var(--dark-bg);
  color: var(--youtube);
  height: 100vw;
}

img {
  transition: transform 0.5s ease-in-out;
}

.card {
  border: 0.3125rem solid;
  position: fixed;
  z-index: 99;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  padding: 3.125rem;
  padding-bottom: 1.875rem;
  text-align: center;
  width: 37.5rem;
  background-color: aqua;
}
.card_buttons {
  padding-top: 1.875rem;
  padding-bottom: 1.875rem;
}
.card_yes, .card_no {
  width: 12.5rem;
  height: 3.125rem;
  text-align: center;
  font-size: 2.5rem;
  font-weight: bold;
}
.card_sure {
  padding-top: 2.5rem;
}